export default {
	/**
	 * 数据
	 */
	props: {
		// 列表数据
		list: {
			type: Array,
			default() {
				let data = [];
				for (let index = 1; index <= 13; index++) {
					data.push({
						id: index,
						img: `../../static/images/${index}.jpg`
					});
				}
				return data;
			}
		},

		// 列数
		count: {
			type: Number,
			default: 2
		},

		// 列间距
		gap: {
			type: String,
			default: '10px'
		}
	},

	/**
	 * 方法
	 */
	methods: {
		/**
		 * item 点击事件
		 * @param {Object} item 数据对象
		 * @param {Number} index 数据下标
		 */
		onTapItem(item, index) {
			this.$emit('tapItem', item, index);
		}
	}
};
